@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.max-center {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}
body {
  background-color: #090a0e;
  color: #fff;
}

header {
  display: flex;
  justify-content: space-between;
  margin: 1em 2em;
  align-items: center;
}

.container {
  max-width: 1000px;
  margin-left: auto;
  padding: 15px;
  margin-right: auto;
  display: grid;
  background-color: #0f1014;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.movie-card {
  margin: 20px;
}

.movie-cover {
  width: 100%;
}
.movie-title {
}

.movie-placeholder {
  min-height: 290px;
  background-color: #fff;
  color: #0f1014;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-box {
  font-size: 14px;
  padding: 8px;
  border: 2px solid #fff;
  /* border: 0; */
  border-radius: 50px 0px 0px 50px;
}

.search-btn {
  padding: 9px;
  font-size: 14px;
  background-color: #2745b0;
  color: #fff;
  border: 1px solid #2745b0;
  outline: none;
  cursor: pointer;
  border-radius: 0px 50px 50px 0px;
  transition: all ease 0.4s;
}

.search-btn:hover {
  background-color: #2745b0b8;
}

.hero {
  position: relative;
  min-height: 500px;
  background-position: top center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
}

.hero-content {
  max-width: 70%;
  background-color: rgb(0 0 0 / 60%);
  padding: 60px;
  border-radius: 125px 0px;
  margin-bottom: 20px;
}

.hero-title {
  font-size: 72px;
  margin: 0;
}

.btn {
  background-color: rgb(39 69 176);
  color: #fff;
  border: 1px solid #fff;
  outline: none;
  padding: 10px 15px;
  margin-bottom: 12px;
  cursor: pointer;
  transition: all ease 0.4s;
}

.btn:hover {
  border-radius: 20px;
}

a{
  text-decoration: none;
  color: #2745b0;
}

.youtube-container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.close-btn {
  background-color: #0f1014;
  position: absolute;
  bottom: 30px;
  left: 30px;
  z-index: 10;
}

footer {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
  justify-content: space-between;
  margin: 1em 2em;
  align-items: center;
}

/* for mobile phone */
@media only screen and (max-width: 1072px) {
  header {
    justify-content: space-around;
  }
  footer {
    justify-content: space-around;
    font-size: 14px;
  }
}

@media only screen and (max-width: 600px) {
  .logo {
    font-size: 20px;
  }

  .search-box {
    font-size: 7px;
  }

  .search-btn {
    font-size: 7px;
  }

  .hero {
    min-height: 420px;
  }
  .container {
    grid-template-columns: auto auto;
  }

  .movie-title {
    font-size: 12px;
  }

  .hero-content {
    max-width: 98%;
    padding: 42px;
  }

  .movie-title {
    font-size: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  .movie-desc {
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; /* number of lines to show */
    line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  .btn {
    padding: 8px 12px;
  }

}

